DifferentSizes.parameters = { storySource: { source: "(args: ButtonProps): React.ReactElement => (\n  <Flex className=\"buttons\" direction=\"column\">\n    <Box boxMargin={{ bottom: \"md\" }}>\n      <Button {...args} onClick={action(\"lg button click\")} size=\"lg\">\n        Large\n      </Button>\n    </Box>\n    <Box boxMargin={{ bottom: \"md\" }}>\n      <Button {...args} onClick={action(\"sm button click\")} size=\"sm\">\n        Small\n      </Button>\n    </Box>\n    <Box>\n      <Button {...args} onClick={action(\"full button click\")} size=\"full\">\n        Full\n      </Button>\n    </Box>\n  </Flex>\n)" }, ...DifferentSizes.parameters };
DisabledUse.parameters = { storySource: { source: "(): React.ReactElement => (\n  <Flex justifyContent=\"space-around\" className=\"buttons\">\n    <Button variant=\"primary\" disabled>\n      Primary\n    </Button>\n    <Button variant=\"secondary\" disabled>\n      Secondary\n    </Button>\n    <Button variant=\"tertiary\" disabled>\n      Tertiary\n    </Button>\n    <Button variant=\"quaternary\" disabled>\n      Quaternary\n    </Button>\n    <Button intent=\"muted\" disabled>\n      Muted\n    </Button>\n  </Flex>\n)" }, ...DisabledUse.parameters };
DisabledWithTooltip.parameters = { storySource: { source: "(): React.ReactElement => (\n  <Flex justifyContent=\"space-around\" className=\"buttons\">\n    <UncontrolledTooltip label=\"launching is disabled for this archived campaign\">\n      <Button variant=\"primary\" disabled>\n        Launch Campaign\n      </Button>\n    </UncontrolledTooltip>\n    <Annotation\n      label=\"There was a problem deleting this current. Refresh your page and try again.\"\n      variant=\"error\"\n    >\n      <Button variant=\"primary\" disabled>\n        Delete Current\n      </Button>\n    </Annotation>\n  </Flex>\n)" }, ...DisabledWithTooltip.parameters };
LoadingStateWSuccess.parameters = { storySource: { source: "(args: ButtonProps): React.ReactElement => {\n  const [status, setStatus] = useState<ButtonLoadingState>(\"idle\");\n\n  React.useEffect(() => {\n    if (status !== \"loading\") {\n      return;\n    }\n\n    const timeout = setTimeout(() => {\n      setStatus(\"success\");\n    }, 3000);\n\n    return () => {\n      clearTimeout(timeout);\n    };\n  }, [status]);\n\n  const [buttonProps] = useLoadingButton(status);\n\n  return (\n    <Flex justifyContent=\"space-around\" className=\"buttons\">\n      <Button\n        {...buttonProps}\n        {...args}\n        loadingMessage=\"Loading...\"\n        onClick={() => setStatus(\"loading\")}\n      >\n        Primary\n      </Button>\n    </Flex>\n  );\n}" }, ...LoadingStateWSuccess.parameters };
LoadingStateWError.parameters = { storySource: { source: "(args: ButtonProps): React.ReactElement => {\n  const [status, setStatus] = useState<ButtonLoadingState>(\"idle\");\n\n  useEffect(() => {\n    if (status !== \"loading\") {\n      return;\n    }\n\n    const timeout = setTimeout(() => {\n      setStatus(\"error\");\n    }, 3000);\n\n    return () => {\n      clearTimeout(timeout);\n    };\n  }, [status]);\n\n  const [buttonProps, onReset] = useLoadingButton(status);\n\n  const handleReset = () => {\n    onReset();\n\n    setStatus(\"idle\");\n  };\n\n  return (\n    <>\n      <Flex justifyContent=\"space-around\" className=\"buttons\">\n        <Annotation\n          disabled={buttonProps.loadingState !== \"error\"}\n          label=\"There was a problem deleting this current. Refresh your page and try again.\"\n          variant=\"error\"\n        >\n          <Button\n            {...buttonProps}\n            {...args}\n            loadingMessage=\"Loading...\"\n            onClick={() => setStatus(\"loading\")}\n          >\n            Primary\n          </Button>\n        </Annotation>\n      </Flex>\n      <br />\n      <Flex justifyContent=\"space-around\" className=\"buttons\">\n        <Button\n          className=\"reset-loading-state-btn\"\n          variant=\"secondary\"\n          onClick={handleReset}\n        >\n          Reset loading state\n        </Button>\n      </Flex>\n    </>\n  );\n}" }, ...LoadingStateWError.parameters };