Accordion.parameters = { storySource: { source: "({ intent, isOpen }: AccordionArgs) => {\n  return (\n    <BannerAlert isOpen intent={intent}>\n      <BannerAlertBody\n        onClose={() => {\n          action(\"click\");\n\n          alert(\"close!!\");\n        }}\n      >\n        <BannerAlertTitle>Lorem ipsum dolor sit amet</BannerAlertTitle>\n        <BannerAlertLink\n          onClick={() => {\n            action(\"click\");\n\n            alert(\"link!!\");\n          }}\n          caret={isOpen ? \"up\" : \"down\"}\n        >\n          More Information\n        </BannerAlertLink>\n      </BannerAlertBody>\n      <BannerAlertAccordion isOpen={isOpen}>\n        <BannerAlertAccordionHeader>\n          Some Title Here with Information\n        </BannerAlertAccordionHeader>\n        <BannerAlertAccordionBody>\n          Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do\n          eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad\n          minim veniam, quis nostrud exercitation ullamco laboris nisi ut\n          aliquip ex ea commodo consequat. Duis aute irure dolor in\n          reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla\n          pariatur. Excepteur sint occaecat cupidatat non proident, sunt in\n          culpa qui officia deserunt mollit anim id est laborum.\n        </BannerAlertAccordionBody>\n        <BannerAlertAccordionFooter>\n          <BannerAlertAction\n            onClick={() => {\n              action(\"click\");\n\n              alert(\"action!!\");\n            }}\n          >\n            Click Me!\n          </BannerAlertAction>\n        </BannerAlertAccordionFooter>\n      </BannerAlertAccordion>\n    </BannerAlert>\n  );\n}" }, ...Accordion.parameters };
