export const OtherStyling = () => {
  const otherCss = css`
    background-color: aliceblue;
  `;

  return (
    <div
      className="wrapper-display"
      style={{
        border: "1px solid",
        borderColor: getColor("lightBlue", 500)({ theme }),
      }}
    >
      <Box
        boxMargin="lg"
        boxPadding="sm"
        style={{
          border: "1px solid",
          borderColor: getColor("red", 500)({ theme }),
          display: "flex",
          alignItems: "center",
          justifyContent: "space-around",
        }}
        styledCss={otherCss}
      >
        <BodyText
          style={{
            border: "1px solid",
            borderColor: getColor("green", 500)({ theme }),
          }}
        >
          Flex Box 1!
        </BodyText>
        <BodyText
          style={{
            border: "1px solid",
            borderColor: getColor("green", 500)({ theme }),
            height: "100px",
          }}
        >
          Flex Box 2!
        </BodyText>
        <BodyText
          style={{
            border: "1px solid",
            borderColor: getColor("green", 500)({ theme }),
          }}
        >
          Flex Box 3!
        </BodyText>
      </Box>
    </div>
  );
};;

export const SpacingWithoutBox = () => {
  const margin = "sm";
  const padding: BoxSpecs = {
    left: "xxl",
    right: "lg",
    top: "md",
    bottom: "xs",
  };
  const boxStyles = getBoxCss(margin, padding, { theme });

  return (
    <div
      className="wrapper-display"
      style={{
        display: "inline-block",
        border: "1px solid",
        borderColor: getColor("lightBlue", 500)({ theme }),
      }}
    >
      <div
        style={{
          ...boxStyles,
          border: "1px solid",
          borderColor: getColor("red", 500)({ theme }),
        }}
      >
        <BodyText>I need spacing!</BodyText>
      </div>
    </div>
  );
};;

