CombinedProps.parameters = { storySource: { source: "(args: FlexProps): React.ReactElement => {\n  return (\n    <Flex style={{ background: \"pink\", minHeight: \"200px\" }} {...args}>\n      <Box boxPadding=\"sm\" boxMargin=\"sm\" style={{ border: \"1px solid black\" }}>\n        Hi\n      </Box>\n      <Box boxPadding=\"sm\" boxMargin=\"sm\" style={{ border: \"1px solid black\" }}>\n        Hello\n      </Box>\n      <Box boxPadding=\"sm\" boxMargin=\"sm\" style={{ border: \"1px solid black\" }}>\n        Test\n      </Box>\n      <Box boxPadding=\"sm\" boxMargin=\"sm\" style={{ border: \"1px solid black\" }}>\n        <LoremIpsum />\n      </Box>\n    </Flex>\n  );\n}" }, ...CombinedProps.parameters };
InvalidCustomStyles.parameters = { storySource: { source: "({\n  invalidStyle,\n}: {\n  invalidStyle: string;\n}): React.ReactElement => {\n  const outerStyle =\n    invalidStyle === \"none\"\n      ? {\n          background: \"pink\",\n          minHeight: \"200px\",\n        }\n      : {\n          background: \"pink\",\n          minHeight: \"200px\",\n          [invalidStyle]: \"25px\",\n        };\n\n  return (\n    <Flex inline style={outerStyle}>\n      <Box boxPadding=\"sm\" boxMargin=\"sm\" style={{ border: \"1px solid black\" }}>\n        Hi\n      </Box>\n      <Box boxPadding=\"sm\" boxMargin=\"sm\" style={{ border: \"1px solid black\" }}>\n        Hello\n      </Box>\n      <Box boxPadding=\"sm\" boxMargin=\"sm\" style={{ border: \"1px solid black\" }}>\n        Test\n      </Box>\n    </Flex>\n  );\n}" }, ...InvalidCustomStyles.parameters };