BasicUseWithActionButtons.parameters = { storySource: { source: "({\n  closeOnClickInside,\n  placement,\n  variant,\n}: MenuProps) => {\n  const [menuState, targetRef] = useMenu<HTMLButtonElement>();\n\n  return (\n    <React.Fragment>\n      <Flex\n        justifyContent=\"center\"\n        alignItems=\"center\"\n        style={{ height: \"90vh\" }}\n      >\n        <Button\n          ref={targetRef}\n          className=\"open-close-button\"\n          id=\"menu1-button\"\n          aria-haspopup=\"true\"\n          aria-controls=\"menu1\"\n          aria-expanded={menuState.isOpen}\n        >\n          Click to open\n        </Button>\n      </Flex>\n      <Menu\n        {...menuState}\n        targetRef={targetRef}\n        placement={placement}\n        variant={variant}\n        closeOnClickInside={closeOnClickInside}\n        id=\"menu1\"\n        aria-labelledby=\"menu1-button\"\n      >\n        <MenuActionButton id=\"mi1\">User Preview</MenuActionButton>\n        <MenuActionButton id=\"mi2\">CSV Export User Data</MenuActionButton>\n        <MenuActionButton id=\"mi3\">CSV Export Email Address</MenuActionButton>\n        <MenuActionButton id=\"mi4\">\n          Export as Facebook Audience\n        </MenuActionButton>\n        <MenuActionButton disabled id=\"mi32\">\n          Export as Google+ Audience\n        </MenuActionButton>\n      </Menu>\n    </React.Fragment>\n  );\n}" }, ...BasicUseWithActionButtons.parameters };
BasicUseWithCategoryButtons.parameters = { storySource: { source: "({\n  closeOnClickInside,\n  placement,\n  variant,\n}: MenuProps) => {\n  const [menuState, targetRef] = useMenu<HTMLButtonElement>();\n\n  return (\n    <React.Fragment>\n      <Button\n        ref={targetRef}\n        className=\"open-close-button\"\n        id=\"menu2-button\"\n        aria-haspopup=\"true\"\n        aria-controls=\"menu2\"\n        aria-expanded={menuState.isOpen}\n      >\n        Click to open\n      </Button>\n      <Menu\n        {...menuState}\n        targetRef={targetRef}\n        placement={placement}\n        variant={variant}\n        closeOnClickInside={closeOnClickInside}\n        id=\"menu2\"\n        aria-labelledby=\"menu2-button\"\n      >\n        <MenuCategoryButton id=\"mi5\" heading=\"Retention Report\">\n          Identify if users come back over time\n        </MenuCategoryButton>\n        <MenuCategoryButton id=\"mi6\" heading=\"Funnel Report\">\n          Pinpoint where and why users drop off\n        </MenuCategoryButton>\n        <MenuCategoryButton id=\"mi7\" heading=\"Campaign Comparison\">\n          Measure the impact of different campaigns\n        </MenuCategoryButton>\n        <MenuCategoryButton id=\"mi33\" heading=\"Beta Feature\" disabled>\n          This is disabled\n        </MenuCategoryButton>\n      </Menu>\n    </React.Fragment>\n  );\n}" }, ...BasicUseWithCategoryButtons.parameters };
WithActionButtonsIcons.parameters = { storySource: { source: "({\n  closeOnClickInside,\n  placement,\n  variant,\n}: MenuProps) => {\n  const [menuState, targetRef] = useMenu<HTMLButtonElement>();\n\n  return (\n    <React.Fragment>\n      <Button\n        ref={targetRef}\n        className=\"open-close-button\"\n        id=\"menu3-button\"\n        aria-haspopup=\"true\"\n        aria-controls=\"menu3\"\n        aria-expanded={menuState.isOpen}\n      >\n        Click to open\n      </Button>\n      <Menu\n        {...menuState}\n        targetRef={targetRef}\n        placement={placement}\n        variant={variant}\n        closeOnClickInside={closeOnClickInside}\n        id=\"menu3\"\n        aria-labelledby=\"menu3-button\"\n      >\n        <MenuActionButton id=\"mi8\" iconName=\"eye\">\n          User Preview\n        </MenuActionButton>\n        <MenuActionButton id=\"mi9\" iconName=\"file-alt\">\n          CSV Export User Data\n        </MenuActionButton>\n        <MenuActionButton id=\"mi10\" iconName=\"file-alt\">\n          CSV Export Email Address\n        </MenuActionButton>\n        <MenuActionButton id=\"mi11\" iconName={[\"fab\", \"facebook-square\"]}>\n          Export as Facebook Audience\n        </MenuActionButton>\n        <MenuActionButton\n          id=\"mi34\"\n          iconName={[\"fab\", \"google-plus-square\"]}\n          disabled\n        >\n          Export as Google+ Audience\n        </MenuActionButton>\n      </Menu>\n    </React.Fragment>\n  );\n}" }, ...WithActionButtonsIcons.parameters };
