CustomStyling.parameters = { storySource: { source: "(args: FlexBoxProps): React.ReactElement => {\n  const styledCss = css`\n    border: 1px solid;\n    border-color: #d32f4c;\n    background-color: palegoldenrod;\n  `;\n\n  return (\n    <div\n      className=\"wrapper-display\"\n      style={{\n        display: \"inline-block\",\n        border: \"1px solid\",\n        borderColor: \"#008294\",\n      }}\n    >\n      <FlexBox boxMargin=\"md\" boxPadding=\"xl\" styledCss={styledCss} {...args}>\n        <BodyText>This has custom styling via styled components</BodyText>\n      </FlexBox>\n    </div>\n  );\n}" }, ...CustomStyling.parameters };