HeadingSizeWithH3.parameters = { storySource: { source: "({\n  level,\n  ...args\n}: Args): React.ReactElement => {\n  const [isOpen, setOpen] = React.useState(false);\n\n  const toggle = () => {\n    setOpen(!isOpen);\n  };\n\n  return (\n    <React.Fragment>\n      <Button onClick={() => setOpen(true)}>Show modal</Button>\n      <Modal {...args} isOpen={isOpen} toggle={toggle}>\n        <ModalHeading level={level}>\n          Add New App to Stopwatch &amp; Droidboy\n        </ModalHeading>\n        <ModalBody>\n          <FieldLabel label=\"App name\">\n            <Input id=\"exampleInput\" />\n          </FieldLabel>\n          <FieldLabel label=\"Platform\">\n            <select\n              id=\"exampleInput2\"\n              style={{\n                height: 40,\n                width: 300,\n                paddingLeft: 10,\n                border: \"1px solid #A8B3B8\",\n              }}\n            >\n              <option>Android</option>\n              <option>FireOS</option>\n              <option>iOS</option>\n              <option>Windows</option>\n              <option>Web</option>\n              <option>tvOS</option>\n              <option>Roku</option>\n            </select>\n          </FieldLabel>\n          <FieldLabel label=\"Push Provider\">\n            <RadioButtonGroup name=\"radios\">\n              <RadioButton id=\"radioButton1\" value=\"Firebase (FCM)\">\n                Firebase (FCM)\n              </RadioButton>\n              <RadioButton id=\"radioButton2\" value=\"Huawei (HMS)\">\n                Huawei (HMS)\n              </RadioButton>\n              <RadioButton id=\"radioButton3\" value=\"Baidu\">\n                Baidu\n              </RadioButton>\n            </RadioButtonGroup>\n          </FieldLabel>\n        </ModalBody>\n        <ModalFooter>\n          <ModalFooter aligner side=\"right\">\n            <Button id=\"cancelButton\" variant=\"secondary\" onClick={toggle}>\n              Cancel\n            </Button>\n            <Button id=\"addButton\">Add App</Button>\n          </ModalFooter>\n        </ModalFooter>\n      </Modal>\n    </React.Fragment>\n  );\n}" }, ...HeadingSizeWithH3.parameters };
NestedModals.parameters = { storySource: { source: "(): React.ReactElement => {\n  const [isOpen, setOpen] = React.useState(false);\n  const [isInnerOpen, setInnerOpen] = React.useState(false);\n\n  const toggle = () => {\n    // only close the parent modal if the child modal is closed\n    if (!isInnerOpen) {\n      setOpen(!isOpen);\n    }\n  };\n\n  const toggleInner = () => {\n    setInnerOpen(!isInnerOpen);\n  };\n\n  return (\n    <React.Fragment>\n      <Button onClick={() => setOpen(true)}>Show modal</Button>\n      <Modal size=\"xl\" isOpen={isOpen} toggle={toggle} className=\"outer\">\n        <ModalHeading>You have unsaved changes!</ModalHeading>\n        <ModalBody>\n          <Button onClick={() => setInnerOpen(true)}>Show modal</Button>\n          <Modal\n            size=\"xl\"\n            isOpen={isInnerOpen}\n            toggle={toggleInner}\n            className=\"inner\"\n          >\n            <ModalHeading>A HEADER. NEW THINGS. WHATEVER!</ModalHeading>\n            <ModalBody>\n              <LoremIpsum paragraphs={2} />\n            </ModalBody>\n            <ModalFooter>\n              <ModalFooter aligner side=\"left\">\n                <Button variant=\"quaternary\">Don&apos;t save</Button>\n              </ModalFooter>\n              <ModalFooter aligner side=\"right\">\n                <Button variant=\"secondary\" onClick={toggleInner}>\n                  Keep Editing\n                </Button>\n                <Button>Save Draft</Button>\n              </ModalFooter>\n            </ModalFooter>\n          </Modal>\n          <LoremIpsum paragraphs={7} />\n        </ModalBody>\n        <ModalFooter>\n          <ModalFooter aligner side=\"left\">\n            <Button variant=\"quaternary\">Don&apos;t save</Button>\n          </ModalFooter>\n          <ModalFooter aligner side=\"right\">\n            <Button variant=\"secondary\" onClick={toggle}>\n              Keep Editing\n            </Button>\n            <Button>Save Draft</Button>\n          </ModalFooter>\n        </ModalFooter>\n      </Modal>\n    </React.Fragment>\n  );\n}" }, ...NestedModals.parameters };
NestedModalsWithCustomZIndex.parameters = { storySource: { source: "(): React.ReactElement => {\n  const [isOpen, setOpen] = React.useState(false);\n  const [isInnerOpen, setInnerOpen] = React.useState(false);\n\n  const toggle = () => {\n    // only close the parent modal if the child modal is closed\n    if (!isInnerOpen) {\n      setOpen(!isOpen);\n    }\n  };\n\n  const toggleInner = () => {\n    setInnerOpen(!isInnerOpen);\n  };\n\n  return (\n    <React.Fragment>\n      <Button onClick={() => setOpen(true)}>Show modal</Button>\n      <Modal\n        size=\"xl\"\n        isOpen={isOpen}\n        toggle={toggle}\n        className=\"outer\"\n        zIndex={605}\n      >\n        <ModalHeading>You have unsaved changes!</ModalHeading>\n        <ModalBody>\n          <Button onClick={() => setInnerOpen(true)}>Show modal</Button>\n          <Modal\n            size=\"xl\"\n            isOpen={isInnerOpen}\n            toggle={toggleInner}\n            className=\"inner\"\n          >\n            <ModalHeading>A HEADER. NEW THINGS. WHATEVER!</ModalHeading>\n            <ModalBody>\n              <LoremIpsum paragraphs={2} />\n            </ModalBody>\n            <ModalFooter>\n              <ModalFooter aligner side=\"left\">\n                <Button variant=\"quaternary\">Don&apos;t save</Button>\n              </ModalFooter>\n              <ModalFooter aligner side=\"right\">\n                <Button variant=\"secondary\" onClick={toggleInner}>\n                  Keep Editing\n                </Button>\n                <Button>Save Draft</Button>\n              </ModalFooter>\n            </ModalFooter>\n          </Modal>\n          <LoremIpsum paragraphs={7} />\n        </ModalBody>\n        <ModalFooter>\n          <ModalFooter aligner side=\"left\">\n            <Button variant=\"quaternary\">Don&apos;t save</Button>\n          </ModalFooter>\n          <ModalFooter aligner side=\"right\">\n            <Button variant=\"secondary\" onClick={toggle}>\n              Keep Editing\n            </Button>\n            <Button>Save Draft</Button>\n          </ModalFooter>\n        </ModalFooter>\n      </Modal>\n    </React.Fragment>\n  );\n}" }, ...NestedModalsWithCustomZIndex.parameters };
NonDismissableModal.parameters = { storySource: { source: "(): React.ReactElement => {\n  const [isOpen, setOpen] = React.useState(false);\n\n  const toggle = () => {\n    setOpen(!isOpen);\n  };\n\n  return (\n    <React.Fragment>\n      <Button onClick={() => setOpen(true)}>Show modal</Button>\n      <Modal isOpen={isOpen}>\n        <ModalHeading>You could mess stuff up!</ModalHeading>\n        <ModalBody>What do you think you&apos;re doing?</ModalBody>\n        <ModalFooter>\n          <ModalFooter aligner side=\"right\">\n            <Button intent=\"danger\" onClick={toggle}>\n              I Accept\n            </Button>\n          </ModalFooter>\n        </ModalFooter>\n      </Modal>\n    </React.Fragment>\n  );\n}" }, ...NonDismissableModal.parameters };
NonDismissableModalWithNoExit.parameters = { storySource: { source: "(): React.ReactElement => {\n  const [seconds, setSeconds] = React.useState(10);\n  const [isOpen, setOpen] = React.useState(false);\n\n  const toggle = useCallback(() => {\n    setOpen(!isOpen);\n\n    setSeconds(10);\n  }, [isOpen]);\n\n  React.useEffect(() => {\n    let interval;\n\n    if (isOpen) {\n      interval = setInterval(() => {\n        setSeconds((seconds) => (seconds > 0 ? seconds - 1 : seconds));\n      }, 1000);\n    } else {\n      clearInterval(interval);\n    }\n\n    return () => clearInterval(interval);\n  }, [isOpen]);\n\n  React.useEffect(() => {\n    if (seconds === 0) {\n      toggle();\n    }\n  }, [seconds, toggle]);\n\n  return (\n    <React.Fragment>\n      <Button onClick={() => setOpen(true)}>Show modal</Button>\n      <Modal isOpen={isOpen}>\n        <ModalHeading>You could mess stuff up!</ModalHeading>\n        <ModalBody>{seconds} second(s) until Modal close</ModalBody>\n      </Modal>\n    </React.Fragment>\n  );\n}" }, ...NonDismissableModalWithNoExit.parameters };
ModalWithOutsideContent.parameters = { storySource: { source: "(): React.ReactElement => {\n  const [isOpen, setOpen] = React.useState(false);\n\n  const toggle = () => {\n    setOpen(!isOpen);\n  };\n\n  return (\n    <React.Fragment>\n      <Button onClick={() => setOpen(true)}>Show modal</Button>\n      <Modal size=\"lg\" isOpen={isOpen} toggle={toggle}>\n        <ModalHeading>You have unsaved changes!</ModalHeading>\n        <ModalBody>\n          <Portal style={{ zIndex: 2400 }}>\n            <div\n              className=\"bcl-ignore-click-outside clickMeExample\"\n              style={{\n                zIndex: 2400,\n                position: \"absolute\",\n                padding: \"20px\",\n                background: \"pink\",\n              }}\n            >\n              Click Me\n            </div>\n          </Portal>\n          <Portal style={{ zIndex: 2400 }}>\n            <div\n              className=\"clickMeCloseExample\"\n              style={{\n                zIndex: 2400,\n                position: \"absolute\",\n                top: \"300px\",\n                padding: \"20px\",\n                background: \"pink\",\n              }}\n            >\n              Click Me To Close\n            </div>\n          </Portal>\n          <LoremIpsum paragraphs={5} />\n        </ModalBody>\n        <ModalFooter>\n          <ModalFooter aligner side=\"left\">\n            <Button variant=\"quaternary\">Don&apos;t save</Button>\n          </ModalFooter>\n          <ModalFooter aligner side=\"right\">\n            <Button variant=\"secondary\" onClick={toggle}>\n              Keep Editing\n            </Button>\n            <Button>Save Draft</Button>\n          </ModalFooter>\n        </ModalFooter>\n      </Modal>\n    </React.Fragment>\n  );\n}" }, ...ModalWithOutsideContent.parameters };
ModalWithPopovers.parameters = { storySource: { source: "(): React.ReactElement => {\n  const [isOpen, setOpen] = React.useState(false);\n\n  const toggle = () => {\n    setOpen(!isOpen);\n  };\n\n  const [popoverState, targetRef] = usePopover<HTMLButtonElement>();\n  const [tooltipState, targetTooltipRef] = useTooltip<HTMLButtonElement>();\n\n  return (\n    <React.Fragment>\n      <Button onClick={() => setOpen(true)}>Show modal</Button>\n      <Modal size=\"lg\" isOpen={isOpen} toggle={toggle}>\n        <ModalHeading>You have unsaved changes!</ModalHeading>\n        <ModalBody>\n          <Button className=\"popoverOpen\" ref={targetRef}>\n            Click to open/close\n          </Button>\n          <Popover\n            {...popoverState}\n            targetRef={targetRef}\n            defaultPlacement=\"bottom\"\n          >\n            <PopoverHeading>Heading</PopoverHeading>\n            <PopoverBody>\n              We need to let the user know something in this tooltip!\n            </PopoverBody>\n          </Popover>\n          <Box boxPadding={{ vertical: \"md\" }}>\n            <span id=\"test_text\">Some text should be covered</span>\n          </Box>\n          <LoremIpsum paragraphs={1} />\n          <Button className=\"tooltipOpen\" ref={targetTooltipRef}>\n            Click to open/close tooltip\n          </Button>\n          <Tooltip\n            targetRef={targetTooltipRef}\n            triggers={[\"click\"]}\n            untriggers={[\"click\"]}\n            placement=\"bottom\"\n            {...tooltipState}\n          >\n            Tooltip with some info in it...\n          </Tooltip>\n          <Box boxPadding={{ vertical: \"sm\" }}>\n            <span id=\"test_text2\">Some text should be covered</span>\n          </Box>\n          <LoremIpsum paragraphs={1} />\n        </ModalBody>\n        <ModalFooter>\n          <ModalFooter aligner side=\"left\">\n            <Button variant=\"quaternary\">Don&apos;t save</Button>\n          </ModalFooter>\n          <ModalFooter aligner side=\"right\">\n            <Button variant=\"secondary\" onClick={toggle}>\n              Keep Editing\n            </Button>\n            <Button>Save Draft</Button>\n          </ModalFooter>\n        </ModalFooter>\n      </Modal>\n    </React.Fragment>\n  );\n}" }, ...ModalWithPopovers.parameters };