WithProgrammaticDismissal.parameters = { storySource: { source: "() => {\n  const [selectedOption, setSelectedOption] = useState(options[0]);\n\n  const toastId = useRef<string | number | null>(null);\n\n  const handleSelect = (event) => {\n    setSelectedOption(\n      options.find(({ id }) => event.target.value === id) as any\n    );\n  };\n\n  return (\n    <>\n      <Toaster id=\"toaster-4\" />\n      <Box boxMargin={{ bottom: \"md\" }}>\n        <FieldLabel label=\"Toast type\">\n          <select\n            className=\"toast-type-select\"\n            onBlur={handleSelect}\n            style={{ maxWidth: 300 }}\n          >\n            {options.map((option) => (\n              <option key={option.id} value={option.id}>\n                {option.label}\n              </option>\n            ))}\n          </select>\n        </FieldLabel>\n      </Box>\n      <ButtonGroup>\n        <Button\n          className=\"make-toast\"\n          onClick={() => {\n            toastId.current = makeToast(\n              selectedOption.label,\n              <div>Loading...</div>,\n              {\n                containerId: \"toaster-4\",\n                autoClose: false,\n              }\n            );\n          }}\n        >\n          Make toast\n        </Button>\n        <Button\n          variant=\"secondary\"\n          className=\"dismiss-toast\"\n          onClick={() => {\n            if (toastId.current) {\n              dismissToast(toastId.current);\n\n              toastId.current = null;\n            }\n          }}\n        >\n          Dismiss most recent toast\n        </Button>\n        <Button\n          variant=\"secondary\"\n          className=\"dismiss-all-toasts\"\n          onClick={() => dismissToast()}\n        >\n          Dismiss all toasts\n        </Button>\n      </ButtonGroup>\n    </>\n  );\n}" }, ...WithProgrammaticDismissal.parameters };